import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/trabalhadorExample';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const TrabalhadorIS: FC<InputProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const getSearchExpressions = (): SearchExpression[] => {
    let result = null;

    if (props.inputSearchProps.searchExpressions) {
      result = props.inputSearchProps.searchExpressions();
    } else {
      result = [
        {
          fieldName: 'empresaMFP.id',
          value: globalReducer.globalParameter.empresaMFP.id,
        },
      ];
    }

    return result;
  };

  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'identidade',
            text: 'RG',
          },
          {
            dataField: 'carteiraTrabalhoNumero',
            text: 'Nº Carteira Trab.',
          },
          {
            dataField: 'cpf',
            text: 'CPF',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchExpressions: getSearchExpressions,
        searchViewId: 'TrabalhadorSearchView',
      }}
    />
  );
};

export default TrabalhadorIS;
